import React from 'react'
import WorkCard from './ui/WorkCard'

export default function Quotes() {
    return (
        <div className="cont" id="quoteP">
            <h2>Projects</h2>
            <div>
                <WorkCard />
                <WorkCard />
                <WorkCard />
                <WorkCard />
            </div>
            <style jsx>{`
                .cont {
                    width: 100%;
                    height: auto;
                    position: relative;
                    display: grid;
                   text-align: center;
                   border-top: 30px solid #010101;
                   color: #ffffff;
                   background: #010101;
                //    padding: 0 5%;
                }
                h2{
                    border-radius: 16px;
                    padding: 30px 200px; 
                    font-size: 3em;
                    margin: 40px auto;
                }
            `}</style>
        </div>
    )
}
